import {
	appointDetail
} from "../../../api/user.js"
export default {
	data() {
		return {
			id: '', // 工单id或售后单id
			objInfo: {}
		}
	},
	onLoad(opts) {
		uni.hideShareMenu() 
		
		this.id = opts.id
		this.getAppointDetail() 
	},
	methods: {
		// 获取预约详情
		getAppointDetail() {
			uni.showLoading()
			appointDetail({
				data: {
					id: this.id
				} 
			}).then(res => {
				uni.hideLoading()
				if (res.data.code == 0) {
					this.objInfo = res.data.result
				}
			})
		},
		// 跳转到服务类目详情
		openPage(p,id){
			uni.navigateTo({
				url:`/pages/user/${p}/${p}/?id=${id}`
			})
		},
		// 打电话
		openTel(){
			uni.makePhoneCall({
				phoneNumber: this.objInfo.service_number //仅为示例
			});
		}
	}
}
